import http from '@/components/utils/http.js';
import share from '@/components/share';
export default{
	components: {
		share
	},
	data() {
		return {
			cartcount:0,
			server:this.apiServer,
			specClass: 'none',
			specSelected:[],
			favorite: false,
			shareList: [],
			pinfo:[],
			thumblist:[],
			desc: "",
			sharedata:[],
			
		};
	},
	async onLoad(options){
		
		//接收传值,id里面放的是标题，因为测试数据并没写id 
		let psid = options.psid;
		
		
		let opts = {
			url: '/productApi/getproductinfo/',
			method: 'post'
		};
		
		let param = {psid:psid};
		http.httpTokenRequest(opts, param).then(
			res => {
				//打印请求返回的数据
				if (res.data['code'] == 0) {
					this.pinfo = res.data["pinfo"];
					this.thumblist = res.data["thumblist"];
					this.desc=this.pinfo["content"];
					this.favorite=res.data["isfav"];
					this.cartcount=res.data["cartcount"];
					this.sharedata={title:res.data["pinfo"].title,img:res.data["pinfo"].images}
				} else {
					uni.showToast({title: res.data.msg,icon: 'none'});
				}
			},
			error => {
				console.log(error);
			}
		);
		
		this.shareList = await this.$api.json('shareList');
	},
	
	onShareAppMessage: function( options ){
	　　var that = this;
	　　// 设置菜单中的转发按钮触发转发事件时的转发内容
	　　var shareObj = {
	　　　　title: this.pinfo.title,        // 默认是小程序的名称(可以写slogan等)
	　　　　path: '/pages/product/product?psid='+this.pinfo.psdi,        // 默认是当前页面，必须是以‘/’开头的完整路径
	　　　　imageUrl: '',     //自定义图片路径，可以是本地文件路径、代码包文件路径或者网络图片路径，支持PNG及JPG，不传入 imageUrl 则使用默认截图。显示图片长宽比是 5:4
	　　　　success: function(res){
	　　　　　　// 转发成功之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:ok'){
		
	　　　　　　}
	　　　　},
	　　　　fail: function(){
	　　　　　　// 转发失败之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:fail cancel'){
	　　　　　　　　// 用户取消转发
	　　　　　　}else if(res.errMsg == 'shareAppMessage:fail'){
	　　　　　　　　// 转发失败，其中 detail message 为详细失败信息
	　　　　　　}
	　　　　},
	　　　　complete: function(){
	　　　　　　// 转发结束之后的回调（转发成不成功都会执行）
	　　　　}
	　　};
	　　// 来自页面内的按钮的转发
	　　if( options.from == 'button' ){
	　　　　var eData = options.target.dataset;
	　　　　console.log( eData.name );     // shareBtn
	　　　　// 此处可以修改 shareObj 中的内容
	　　　　shareObj.path = '/pages/btnname/btnname?btn_name='+eData.name;
	　　}
	　　// 返回shareObj
	　　return shareObj;
	},
	methods:{
		
		//分享
		share(){
			this.$refs.share.toggleMask();	
		},
		//收藏
		toFavorite(){
			let opts = {
				url: '/productApi/toFavorite/',
				method: 'post'
			};
			let param = {psid:this.pinfo["psid"]};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						
						this.favorite=res.data["isfav"];
					} else {
						uni.showToast({title: res.data.msg,icon: 'none'});
					}
				},
				error => {
					console.log(error);
				}
			);
			
			
			
			
		},
		buy(){
			uni.navigateTo({
				url: `/pages/order/createOrder?psid=${this.pinfo.psid}`
			})
		},
		
		toChart(){
			uni.navigateTo({
				url: `/pages/cart/home`
			})
		},
		
		addcart(){
			let opts = {
				url: '/productApi/addcart/',
				method: 'post'
			};
			let param = {psid:this.pinfo["psid"]};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						this.cartcount=res.data["cartcount"];
						uni.showToast({title: "成功加入购物车",icon: 'none'});
					} else {
						uni.showToast({title: res.data.msg,icon: 'none'});
					}
				},
				error => {
					console.log(error);
				}
			);
		
		},
		stopPrevent(){}
	},

}
